/*
    Page: locked screen
*/

#time {
    color: #FFF;
    font-size: 34px;
    margin: 160px auto 30px auto;
    font-weight: bold;
    background: rgba(0, 0, 0, 0.5);
    padding: 10px 0;
    width: 200px;
    border-radius: 4px;
}

#time small {
    font-size: 18px !important;
    font-weight: 100;
}

.lockscreen-text {
    color: #fff !important;
    font-size: 21px;
    position: relative;
    top: -8px !important;
    /* font-family: 'Pacifico', cursive; */
    left: 57px;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: -1px;
}

.lockscreen-name {
    color: #ffffff;
    font-size: 24px;
    text-align: center;
}


/* We will put the dynamically generated digital clock here */

.lockscreen .headline {
    color: #fff;
    text-shadow: 1px 3px 5px rgba(0, 0, 0, 0.5);
    font-weight: 300;
    -webkit-font-smoothing: antialiased !important;
    opacity: 0.8;
    margin: 10px 0 30px 0;
    font-size: 90px;
}

.lockscreen .lockscreen-name {
    text-align: center;
    font-weight: 600;
    font-size: 16px;
}

@media screen and (max-width: 480px) {
    .lockscreen .headline {
        font-size: 60px;
        margin-bottom: 40px;
    }
}


/* User name [optional] */


/* Will contain the image and the sign in form */

.lockscreen-item {
    padding: 0;
    background: #fff;
    position: relative;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
    margin: 10px auto;
    width: 290px;
}

.lockscreen-item:before {
    display: table;
    content: " ";
}

.lockscreen-item:after {
    display: table;
    content: " ";
    clear: both;
}

.lockscreen-item > .lockscreen-image {
    position: absolute;
    left: -10px;
    top: -30px;
    background: #fff;
    padding: 10px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    z-index: 10;
}

.lockscreen-item > .lockscreen-image > img {
    width: 70px;
    height: 70px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
}

.lockscreen-item > .lockscreen-credentials {
    margin-left: 80px;
}

.lockscreen-item > .lockscreen-credentials input {
    border: 0 !important;
}

.lockscreen-item > .lockscreen-credentials .btn {
    background-color: #fff;
    border: 0;
}


/* 
User image 
*/


/* Contains the password input and the login button */


/* Extra to give the user an option to navigate the website [optional]*/

.lockscreen-link {
    margin-top: 30px;
    text-align: center;
}


/*
Buttons
*/

.btn {
    font-weight: 500;
    border: 1px solid transparent;
    font-size: 13px;
    padding: 7px 10px;
}

.dropdown-menu > li > a {
    color: #888;
    font-size: 13px;
}

.btn-group.open .dropdown-toggle {
    -webkit-box-shadow: inset 0 0px 0px rgba(0, 0, 0, 0.125) !important;
    box-shadow: inset 0 0px 0px rgba(0, 0, 0, 0.125) !important;
}

.btn.btn-default {
    background-color: #ffffff;
    color: #666;
    border-color: #ddd;
    border-bottom-color: #ddd;
}

.btn.btn-default:hover,
.btn.btn-default:active,
.btn.btn-default.hover {
    background-color: #fff !important;
}

.btn.btn-default.btn-flat {
    border-bottom-color: #d9dadc;
}

.btn.btn-primary {
    background-color: #3c8dbc;
    border-color: #367fa9;
}

.btn.btn-primary:hover,
.btn.btn-primary:active,
.btn.btn-primary.hover {
    background-color: #367fa9;
}

.btn.btn-success {
    background-color: #0AA699;
    border-color: #0AA699;
}

.btn.btn-success:hover,
.btn.btn-success:active,
.btn.btn-success.hover {
    background-color: #2c9399;
}

.btn-success:hover,
.btn-success:focus,
.btn-success:active,
.btn-success.active {
    color: #fff;
    background-color: #2c9399 !important;
    border-color: #2c9399 !important;
}

.open .dropdown-toggle.btn-success {
    color: #fff;
    background-color: #2c9399 !important;
    border-color: #2c9399 !important;
}

.btn.btn-info {
    background-color: #0090D9;
    border-color: #0090D9;
}

.btn.btn-info:hover,
.btn.btn-info:active,
.btn.btn-info.hover {
    background-color: #0181c1;
    border-color: #0181c1;
}

.btn-info:hover,
.btn-info:focus,
.btn-info:active,
.btn-info.active {
    color: #fff;
    background-color: #0181c1 !important;
    border-color: #0181c1 !important;
}

.open .dropdown-toggle.btn-info {
    color: #fff;
    background-color: #0181c1 !important;
    border-color: #0181c1 !important;
}

.btn.btn-danger {
    background-color: #f56954;
    border-color: #f56954;
}

.btn.btn-danger:hover,
.btn.btn-danger:active,
.btn.btn-danger.hover {
    background-color: #f4543c;
}

.btn-danger:hover,
.btn-danger:focus,
.btn-danger:active,
.btn-danger.active {
    color: #fff;
    background-color: #f4543c !important;
    border-color: #f4543c !important;
}

.open .dropdown-toggle.btn-danger {
    color: #fff;
    background-color: #f4543c !important;
    border-color: #f4543c !important;
}

.btn.btn-warning {
    background-color: #f39c12;
    border-color: #f39c12;
}

.btn.btn-warning:hover,
.btn.btn-warning:active,
.btn.btn-warning.hover {
    background-color: #e08e0b;
}

.btn-warning:hover,
.btn-warning:focus,
.btn-warning:active,
.btn-warning.active {
    color: #fff;
    background-color: #e08e0b !important;
    border-color: #e08e0b !important;
}

.open .dropdown-toggle.btn-warning {
    color: #fff;
    background-color: #e08e0b !important;
    border-color: #e08e0b !important;
}

.btn.btn-flat {
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    border-radius: 0;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
    border-width: 1px;
}

.btn:active {
    -webkit-box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
    -moz-box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
    box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
}

.btn:focus {
    outline: none;
}

.btn.btn-file {
    position: relative;
    width: 120px;
    height: 35px;
    overflow: hidden;
}

.btn.btn-file > input[type='file'] {
    display: block !important;
    width: 100% !important;
    height: 35px !important;
    opacity: 0 !important;
    position: absolute;
    top: -10px;
    cursor: pointer;
}

.btn.btn-app {
    position: relative;
    padding: 15px 5px;
    margin: 0 0 10px 10px;
    min-width: 80px;
    height: 60px;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
    text-align: center;
    color: #666;
    border: 1px solid #ddd;
    background-color: #fafafa;
    font-size: 12px;
}

.btn.btn-app > .fa,
.btn.btn-app > .glyphicon,
.btn.btn-app > .ion {
    font-size: 20px;
    display: block;
}

.btn.btn-app:hover {
    background: #f4f4f4;
    color: #444;
    border-color: #aaa;
}

.btn.btn-app:active,
.btn.btn-app:focus {
    -webkit-box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
    -moz-box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
    box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
}

.btn.btn-app > .badge {
    position: absolute;
    top: -3px;
    right: -10px;
    font-size: 10px;
    font-weight: 400;
}

.btn.btn-social-old {
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
    opacity: 0.9;
    padding: 0;
}

.btn.btn-social-old > .fa {
    padding: 10px 0;
    width: 40px;
}

.btn.btn-social-old > .fa + span {
    border-left: 1px solid rgba(255, 255, 255, 0.3);
}

.btn.btn-social-old span {
    padding: 10px;
}

.btn.btn-social-old:hover {
    opacity: 1;
}

.btn.btn-circle {
    width: 30px;
    height: 30px;
    line-height: 30px;
    padding: 0;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
}


/*
Element
 */

h2.box-title {
    font-size: 13px;
    padding: 0 0 15px;
    margin: 45px 0 50px;
    border-bottom: 1px solid #eee;
    color: #888;
}


/*
Gmap
 */

.gmap3 {
    margin: 0 auto 10px auto;
