    transition: all 0.5s ease;
    width: 10%;
    list-style: none;
    font-size: 20px;
    color: #363E49;
}

.entypo li:hover,
.entypo-tooltip li:hover {
    color: #000;
    cursor: pointer;
}

.the-icons {
    line-height: 24px;
    color: #58719C !important;
}

.fontawesome-icon-list {
    color: #58719C !important;
}

.fontawesome-icon-list span {
    color: #888 !important;
    font-size: 13px;
}

.switch input {
    margin-right: 0.3em;
}

.codesOn .i-name {
    display: none;
}

.codesOn .i-code {
    display: inline;
}

.i-code {
    display: none;
}

.i-name {
    font-size: 13px;
