}


/*
Pricing Table
 */

.wrap-price-table {
    margin-top: 60px;
}

.plans {
    width: 836px;
    margin: 50px auto;
    overflow: hidden;
}

.plan {
    float: left;
    width: 100%;
    margin: 20px 2px;
    padding: 15px 25px;
    text-align: center;
    background: white;
    background-clip: padding-box;
    border-color: rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    border: 1px solid #eee;
}

.plan-title {
    margin-bottom: 12px;
    font-size: 19px;
    color: #454545;
    text-transform: uppercase;
    font-family: "Montserrat Hairline", sans-serif !important;
}

.plan-price {
    margin-bottom: 20px;
    line-height: 1;
    font-size: 40px !important;
    font-weight: bold;
    color: #000;
}

.plan-unit {
    display: block;
    margin-top: 5px;
    font-size: 13px;
    font-weight: normal;
    color: #aaa;
}

.plan-features {
    width: 80%;
    margin: 20px auto 15px;
    padding: 15px 0 0 15px;
    border-top: 1px solid #e5ded6;
    text-align: left;
}

.plan-feature {
    line-height: 20px;
    font-size: 24px;
    font-weight: bold;
    list-style: none;
}

.plan-feature + .plan-feature {
    line-height: 20px;
    font-size: 24px;
    font-weight: bold;
    list-style: none;
}

.plan-feature-unit {
    margin-left: 2px;
    font-size: 16px;
}

.plan-feature-name {
    font-size: 13px;
    font-weight: normal;
    color: #aaa;
}

.plan-button {
    position: relative;
    display: block;
    line-height: 40px;
    font-size: 16px;
    font-weight: 500;
    color: white;
    text-align: center;
    text-decoration: none;
    text-shadow: 0 1px rgba(0, 0, 0, 0.1);
    background: #17335A;
    /**/
    border-bottom: 2px solid #cf7e3b;
    border-color: rgba(0, 0, 0, 0.15);
    border-radius: 4px;
}

.plan-button:active {
    top: 2px;
    margin-bottom: 2px;
    border-bottom: 0;
}

.plan-highlight {
    margin-top: 15px;
    margin-bottom: 0;
    padding-left: 15px;
    padding-right: 15px;
    width: 100%;
}

.plan-highlight .plan-button {
    font-size: 18px;
    line-height: 49px;
    background: #1C5E98;
    border-color: rgba(0, 0, 0, 0.15);
}

.plan-recommended {
    width: 100%;
    margin: -15px auto 15px;
    padding-bottom: 2px;
    line-height: 22px;
    font-size: 14px;
    font-weight: bold;
    color: white;
    text-shadow: 0 1px rgba(0, 0, 0, 0.05);
    background: #1C5E98;
    border-radius: 0 0 4px 4px;
}

.plan-circle {
    background: #17335A;
    margin: 0 auto;
    width: 165px;
    height: 165px;
    border-radius: 50%;
    color: #fff !important;
}

.plan-circle h3 {
    color: #fff;
    padding: 35px 0 0 0;
    font-size: 16px;
    text-transform: uppercase;
}

.plan-circle p {
    margin-bottom: 0;
    line-height: 1;
    font-size: 40px !important;
    font-weight: bold;
    color: #fff;
}

.plan-circle span {
    margin: 0;
}

.plan-circle-highlight {
    background: #1C5E98;
    margin: 0 auto;
    width: 150px;
    height: 150px;
    border-radius: 80px;
    color: #fff !important;
}

.plan-circle-highlight h3 {
    color: #fff;
    padding: 35px 0 0 0;
    font-size: 16px;
    text-transform: uppercase;
}

.plan-circle-highlight p {
    margin-bottom: 0;
    line-height: 1;
    font-size: 40px !important;
    font-weight: bold;
    color: #fff;
}

.plan-circle-highlight span {
    margin: 0;
}


/*
Timeline
 */

.timeline {
    margin: 0 0 30px 0;
    padding: 0;
    list-style: none;
}

.timeline:before {
    content: '';
    position: absolute;
    top: 0px;
    bottom: 0;
    width: 2px;
    background: #d2dae1;
    left: 32px;
    border: 1px solid #eee;
    margin: 0;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    border-radius: 2px;
    top: 20px;
}

.timeline > li {
    position: relative;
    margin-right: 10px;
    margin-bottom: 15px;
}

.timeline > li:before {
    display: table;
    content: " ";
}

.timeline > li:after {
    display: table;
    content: " ";
    clear: both;
}

.timeline > li > .timeline-item {
    margin-top: 25px;
    border: 0px solid #dfdfdf;
    background: #fff;
    color: #888;
    margin-left: 60px;
    margin-right: 0;
    padding: 0;
    position: relative;
    line-height: 1.6em;
    font-size: 13px;
}

.timeline > li > .timeline-item > .time {
    color: #999;
    float: right;
    margin: 2px 0 0 0;
}

.timeline > li > .timeline-item > .timeline-header {
    margin: 0 0 10px;
    border-bottom: 1px dashed #ddd;
    padding: 20px;
    font-size: 14px;
    line-height: 1.1;
}

.timeline > li > .timeline-item > .timeline-header span {
    color: #fff;
    font-size: 13px;
    font-weight: bold;
}

.timeline > li > .timeline-item > .timeline-header:before {
    content: "\f0d9";
    font-size: 60px;
    font-family: FontAwesome;
    position: absolute;
    left: -15px;
}

.timeline > li > .timeline-item > .timeline-header > a {
    font-weight: 600;
}

.timeline > li > .timeline-item > .timeline-body,
.timeline > li > .timeline-item > .timeline-footer {
    padding: 5px 10px;
    margin: 0;
}

.timeline > li.time-label > span {
    font-weight: 600;
    padding: 5px;
    display: inline-block;
    background-color: #C2CBD1;
    color: #ffffff;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
}

.timeline > li > .fa,
.timeline > li > .glyphicon,
.timeline > li > .ion {
    width: 45px;
    height: 45px;
    font-size: 25px;
    line-height: 45px;
    position: absolute;
    color: #fff;
    background: #c2cbd1;
    border-radius: 50%;
    text-align: center;
    left: -5px;
    top: 20px;
}

.timeline > li > h2 {
    font-size: 12px;
}

.timeline > li > span {
    color: #666;
    margin: 0;
    position: relative;
    left: 60px;
    top: 5px;
}

.timeline > li > span > small {
    background: none repeat scroll 0 0 #b1c2ca;
    border-radius: 3px;
    color: #fff;
    float: right;
    margin: 0;
    padding: 3px 5px;
    text-align: right;
    top: 0;
}

.timeline-body span {
    display: block;
}

.timeline-body span small {
    float: right;
}

.timeline-body p {
    color: #ffffff !important;
}


/*
404 & 500
 */

.edumix-not-found {
    text-align: center;
    margin: 50px auto 0 auto;
}

.edumix-not-found h1 {
    margin: 0;
    font-size: 60px;
}

.edumix-not-found i {
    font-size: 60px;
}

.edumix-not-found span {
    background: #454545;
    color: #fff;
    text-transform: uppercase;
    height: 35px;
    display: inline-table;
    padding: 0 10px;
    line-height: 35px;
    margin: 0 0 15px;
    border-radius: 3px;
}

section.page-error {
    margin: 100px auto;
    width: 380px;
    background: rgba(255, 255, 255, 0.5);
    padding: 40px;
    border-radius: 4px;
}


/* 
ALERT 
*/


/*                                        */


/* Alert Panel HTML Classes */


/*                                        */


/* Alert panel container */

.alert-panel {
    margin-bottom: 1.5em;
    line-height: 1.3em;
}

.alert-panel p,
.alert-panel a,
.alert-panel h1,
.alert-panel h2,
.alert-panel h3,
.alert-panel h4,
.alert-panel h5,
.alert-panel h6 {
    margin-bottom: 0;
    margin-top: 0;
}

.alert-panel .button.secondary {
    background: #fff;
}


/* Alert panel title */

.alert-panel-title {
    background-color: #1688EC;
    -moz-border-radius-topleft: 3px;
    -webkit-border-top-left-radius: 3px;
    border-top-left-radius: 3px;
    -moz-border-radius-topright: 3px;
    -webkit-border-top-right-radius: 3px;
    border-top-right-radius: 3px;
    font-size: 14px;
    color: #fff;
    font-weight: bold;
    line-height: 1.3em;
    position: relative;
    padding: 10px 34px 10px 20px;
    display: block;
    font-family: 'Montserrat', sans-serif;
}


/* Alert panel close button */

.alert-panel.close {
    /* border-bottom: solid 1px #2284a1; */
    -moz-border-radius-topright: 2px;
    -webkit-border-top-right-radius: 2px;
    border-top-right-radius: 2px;
    -moz-border-radius-bottomleft: 2px;
    -webkit-border-bottom-left-radius: 2px;
    border-bottom-left-radius: 2px;
    position: absolute;
    top: 0px;
    right: 0px;
    padding: 10px 15px;
    line-height: -4px;
    font-size: 0.8em;
    font-weight: bold;
    -webkit-transition: all, 300ms;
    -moz-transition: all, 300ms;
    -o-transition: all, 300ms;
    transition: all, 300ms;
    background-color: #F20556;
    color: #fff;
}

.alert-panel.close:hover,
.alert-panel.close:active {
    background-color: #2284a1;
    border-bottom: solid 1px #2284a1;
    border-left: solid 1px #2284a1;
    color: #cdebf5;
}

.alert-panel.secondary .alert-panel.close {
    background-color: rgba(0, 0, 0, 0.1);
    color: rgba(255, 255, 255, 0.4);
}

.alert-panel.secondary .alert-panel.close:hover,
.alert-panel.secondary .alert-panel.close:active {
    background-color: #c3c3c3;
    border-bottom: solid 1px #c3c3c3;
    border-left: solid 1px #c3c3c3;
    color: white;
}

.alert-panel.success .alert-panel.close {
    background-color: rgba(0, 0, 0, 0.1);
    color: rgba(255, 255, 255, 0.4);
}

.alert-panel.success .alert-panel.close:hover,
.alert-panel.success .alert-panel.close:active {
    background-color: #457a1a;
    border-bottom: solid 1px #457a1a;
    border-left: solid 1px #457a1a;
    color: #c6eca7;
}

.alert-panel.alert .alert-panel.close {
    background-color: rgba(0, 0, 0, 0.1);
    color: rgba(255, 255, 255, 0.4);
}

.alert-panel.alert .alert-panel.close:hover,
.alert-panel.alert .alert-panel.close:active {
    background-color: #970b0e;
    border-bottom: solid 1px #970b0e;
    border-left: solid 1px #970b0e;
    color: #f8a9aa;
}


/* Alert panel message area */

.alert-panel-message {
    background: rgba(255, 255, 255, 0.75);
    padding: 20px;
    line-height: 1.3;
    margin: 0 0 30px;
    border-radius: 0 0 4px 4px;
}


/* Alert panel action area */

.alert-panel-action {
    background: #e3f4f9;
    -moz-border-radius-bottomleft: 3px;
    -webkit-border-bottom-left-radius: 3px;
    border-bottom-left-radius: 3px;
    -moz-border-radius-bottomright: 3px;
    -webkit-border-bottom-right-radius: 3px;
    border-bottom-right-radius: 3px;
    padding: 10px;
    text-align: right;
    margin: 0 0 30px;
}

.alert-panel.secondary .alert-panel-action {
    background: #f6f6f6;
}

.alert-panel.success .alert-panel-action {
    background: #D7EDEC;
}

.alert-panel.alert .alert-panel-action {
    background: #fac0c2;
    border: solid 1px #ef3236;
}


